* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

section {
    position: -webkit-sticky; /* Required for Safari */
    position: sticky;
    height: auto;
    top: 0;
    overflow: visible;
    padding: 0;
    margin: 0;
    
}

body {
    height: 100vh;
    margin: 0;
    font-family: 'cursive', 'Arial Narrow', Arial, sans-serif;
    background-image: url(https://images.unsplash.com/photo-1603075405944-701034a7534a?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1031&q=80);
    background-position: center;
    background-position-y: top;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

a {
    text-decoration: none;
}

li {
    list-style: none;
}

/*Nav Styling Starts*/
.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    /*Transparent Background color with rgba hack (0,0,0,.5) gives transparent effect*/
    background-color: rgba(0,0,0,0);
    color: #fff;
    position: fixed;
    z-index: +1;
    width: 100%;
}

.nav-links a {
    color: #fff;
}

/* LOGO */
.logo {
    font-size: 32px;
}

/* Navbar Menu */
.menu {
    display: flex;
    gap: 1em;
    font-size: 18px;
}

.menu li:hover {
    background-color: #4c9e9e;
    border-radius: 5px;
    transition: 0.3s ease;
}

.menu li {
    padding: 5px 14px;
}

/* Dropdown Menu */
.services {
    position: relative;
}

.dropdown {
    background-color: rgb(1, 139, 139);
    padding: 1em 0;
    position: absolute; /* With Respect To Parent */
    display: none;
    border-radius: 8px;
    top: 35px;
}

.dropdown li + li {
    margin-top: 10px;
}

.dropdown li {
    padding: 0.5em 1em;
    width: 8em;
    text-align: center;
}

.dropdown li:hover {
    background-color: #4c9e9e;
}

.services:hover .dropdown {
    display: block;
}
/*Nav Bar Styling Ends*/

/*RESPONSIVE NAVBAR MENU STARTS*/
/* CHECKBOX HACK */
input[type=checkbox] {
    display: none;
}

/*Hamburger Menu*/
.hamburger {
    display: none;
    font-size: 24px;
    user-select: none;
}

/*Applying Media Queries*/
@media (max-width: 768px) {
    .menu {
        display: none;
        position: absolute;
        background-color: teal;
        right: 0;
        left: 0;
        text-align: center;
        padding: 16px 0;
    }

    .menu li:hover {
        display: inline-block;
        background-color: #4c9e9e;
        transition: 0.3s ease;
    }

    .menu li + li {
        margin-top: 12px;
    }

    input[type=checkbox]:checked ~ .menu{
        display: block;
    }

    .hamburger {
        display: block;
    }

    .dropdown {
        left: 50%;
        top: 30px;
        transform: translateX(35%);
    }

    .dropdown li:hover {
        background-color: #4c9e9e;
    }
}
/*RESPONSIVE NAVBAR MENU ENDS*/

section {
    height: auto;
    padding: 10em;

    h1 {
        font-size: 3em;
    }

    p {
        font-size: 1.5em;
        
    }

    a {
        color: white;
        font-weight: bold;
    }

    
    &:last-child {
        background: rgb(32, 37, 43);
        color: white;
    }


}

.section1 {
    
    position: sticky;
    padding: 6rem;
    height: 100vh;
    color: white;
}


.custom-p {
    opacity: 0;
    transition: opacity 3s;
}

.scroll-class {
    opacity: 1;
}



